import MainSection from "../../Shared/Components/Sections/MainSection"
import InfoSection from "../../Shared/Components/Sections/SectionTwo"
import MainPhoto from "../../assets/Images/mainSection/Group 32.png"
import SendPhoto from "../../assets/Images/إرسال-الحوالات-التعريف 1.png"

import "../Style/Services.css"
import QuestionPhoto from "../../assets/Images/Group 24.png";
import { useEffect } from "react"

import AOS from "aos";
import "aos/dist/aos.css"; // استيراد CSS الخاصة بـ AOS
const Services = () => {

    const sectionData = {
        title: {   dir: false, highlight: "تفاصيل حياتك اليومية", text: "خدماتنا صُممت لتلائم احتياجاتك وتبسط" },
        highlights: [],
        imageSrc: MainPhoto,
      };
     useEffect(() => {
       AOS.init({
         duration: 800,
         easing: "ease-in-out", 
         once: true,
       });
     }, []);
  return (
    <>
     <MainSection {...sectionData} className="main-services" />
     <InfoSection
      imageSrc={SendPhoto}
      title="إرسال واستلام الحوالات"
      linkText="تعلم  إستخدام الخدمة"
      linkTo="/support"
      className="services"
      dir={true}
    >
        خدمة إرسال واستلام الحوالات عبر <span>القطيبي شلن</span> تتيح لك تحويل الأموال إلى المستلم في ثوانٍ إلى أي شخص في أي مكان، سواء محليًا أو دوليًا. يمكنك استخدام تطبيق شلن على هاتفك الذكي لإجراء التحويلات في أي وقت، دون الحاجة إلى الذهاب إلى فروع مصرفية أو الانتظار في طوابير.
     </InfoSection>
     <InfoSection
      imageSrc={SendPhoto}
      title="سحب وإيداع الأموال"
      linkText="تعلم  إستخدام الخدمة"
      linkTo="/support"
      className="services"
      dir={false}
    >
     خدمة إرسال واستلام الحوالات عبر <span>القطيبي شلن</span> تتيح لك تحويل الأموال إلى المستلم في ثوانٍ إلى أي شخص في أي مكان، سواء محليًا أو دوليًا. يمكنك استخدام تطبيق شلن على هاتفك الذكي لإجراء التحويلات في أي وقت، دون الحاجة إلى الذهاب إلى فروع مصرفية أو الانتظار في طوابير.
     </InfoSection>
     <InfoSection
      imageSrc={SendPhoto}
      title="دفع المشتريات"
      linkText="تعلم  إستخدام الخدمة"
      linkTo="/support"
      className="services"
      dir={true}
    >
      خدمة دفع المشتريات عبر شلن تتيح لك شراء المنتجات أو الخدمات بسهولة وأمان باستخدام محفظتك الإلكترونية. يمكنك الدفع مباشرة من رصيد محفظتك لدى المتاجر أو المواقع الإلكترونية المشتركة في الخدمة، دون الحاجة لاستخدام النقد أو البطاقات البنكية، مما يجعل عمليات الشراء أسرع وأكثر مرونة.
      </InfoSection>
      <InfoSection
      imageSrc={SendPhoto}
      title="تسديد الخدمات والفواتير"
      linkText="تعلم  إستخدام الخدمة"
      linkTo="/support"
      className="services"
      dir={false}
    >
      خدمة دفع المشتريات عبر شلن تتيح لك شراء المنتجات أو الخدمات بسهولة وأمان باستخدام محفظتك الإلكترونية. يمكنك الدفع مباشرة من رصيد محفظتك لدى المتاجر أو المواقع الإلكترونية المشتركة في الخدمة، دون الحاجة لاستخدام النقد أو البطاقات البنكية، مما يجعل عمليات الشراء أسرع وأكثر مرونة.
      </InfoSection>
      <InfoSection
      imageSrc={SendPhoto}
      title="شحن الرصيد"
      linkText="تعلم  إستخدام الخدمة"
      linkTo="/support"
      className="services"
      dir={true}
    >
      خدمة إرسال واستلام الحوالات عبر شلن تتيح لك تحويل الأموال إلى المستلم في ثوانٍ إلى أي شخص في أي مكان، سواء محليًا أو دوليًا. يمكنك استخدام تطبيق شلن على هاتفك الذكي لإجراء التحويلات في أي وقت، دون الحاجة إلى الذهاب إلى فروع مصرفية أو الانتظار في طوابير.
      </InfoSection>
      <InfoSection
      imageSrc={SendPhoto}
      title="المدفوعات الجماعية"
      linkText="تعلم  إستخدام الخدمة"
      linkTo="/support"
      className="services"
      dir={false}
    >
    خدمة دفع المشتريات عبر شلن تتيح لك شراء المنتجات أو الخدمات بسهولة وأمان باستخدام محفظتك الإلكترونية. يمكنك الدفع مباشرة من رصيد محفظتك لدى المتاجر أو المواقع الإلكترونية المشتركة في الخدمة، دون الحاجة لاستخدام النقد أو البطاقات البنكية، مما يجعل عمليات الشراء أسرع وأكثر مرونة.
     </InfoSection>
     <InfoSection
      imageSrc={QuestionPhoto}
      title="هل توجد لديك المزيد من الأسئلة حول خدماتنا؟"
      linkText="تفقد الدعم الفني"
      linkTo="/support"
      className="question"
      dir={true}
    >
      <>"إذا كان لديك أي أسئلة إضافية حول خدمة إرسال واستلام الحوالات أو كنت بحاجة إلى مزيد من التفاصيل، فلا تتردد في زيارة صفحة الدعم الفني لتجد كل الأسئلة التي تبحث عنها, سنكون سعداء بمساعدتك."</>
     </InfoSection>

    </>
  )
}

export default Services
