import GoogleIcon from "../../../assets/Icons/logos_google-play-icon.png";
import AppleIcon from "../../../assets/Icons/Vector.png";
import "../../Style/DownloadLinks.css"
const DownloadLinks = () => {
  return (
    <div className="download_links">
        <a href="https://google.com/" target="_blank">
      <button>
        <span>
          <img src={GoogleIcon} alt="" />
        </span>
       <div className="text">
        <span>حمل التطبيق</span>
        <span>جوجل بلاي </span>
       </div>
      </button>
      </a>
      <a href="https://google.com/" target="_blank">
      <button>
        <span>
          <img src={AppleIcon} alt="" />
        </span>
        <div className="text">
        <span>حمل التطبيق </ span>
        <span>اب ستور</span>
       </div>
      </button>
      </a>
    </div>
  );
};

export default DownloadLinks;
