import "../../Style/MainSection.css";
import { Correct } from "../../../assets/Icons/Correct";
import DownloadLinks from "../Elements/DownloadLinks.";

const MainSection = ({  title, highlights, imageSrc, className ,Video}:any) => {
  return (
    <main  className={`section-container ${className}`}>
      <div className="content-wrapper">
        <div className="content-section">
          <h1 className="section-title">
            {title.dir ?  <><span>{title.highlight}</span> {title.text}</> : 
             <>{title.text} <span>{title.highlight}</span>  </>}
           
          </h1>
          <ul className="list">
            {highlights.map((item:any, index:any) => (
              <li key={index} className="list-item">
                <span className="icon-wrapper">
                  <Correct />
                </span>
                {item}
              </li>
            ))}
          </ul>
          <DownloadLinks />
        </div>
      </div>
      <div data-aos="fade-right" className="image-container">
        <img src={imageSrc} alt="Main Section" />
        {Video && <video  autoPlay loop muted src={Video}></video>}
      </div>
    </main>
  );
};

export default MainSection;
